type MasonryImageAttrs =
  | "sourceWidth"
  | "sourceHeight"
  | "masonryWidth"
  | "masonryHeight"
  | "offsetY"
  | "offsetX";
// 
export class MasonryImage {
  src: string; // src
  sourceWidth?: number; // 
  sourceHeight?: number; // 
  masonryWidth?: number; // 
  masonryHeight?: number; // 
  offsetY?: number; //  top
  offsetX?: number; //  left
  id?: number;

  constructor(
    source: string,
    imageHtmlIns?: HTMLImageElement,
    info?: {
      sourceWidth?: number;
      sourceHeight?: number;
      masonryWidth?: number;
      masonryHeight?: number;
    },
    id?: number
  ) {
    this.src = source;
    this.sourceWidth = info?.sourceWidth;
    this.sourceHeight = info?.sourceHeight;
    this.masonryWidth = info?.masonryWidth;
    this.masonryHeight = info?.masonryHeight;
    this.id = id;
  }

  public setAttributes(attr: MasonryImageAttrs, value: number) {
    this[attr] = value;
  }
}
